
.container-custom {
   width: 100%;
   background-color: #e5e5f7;
   background-image: radial-gradient(#444cf7 3%, transparent 10%), radial-gradient(#444cf7 10%, transparent 10%);
   background-size: 197px 241px;
   background-position: 0 0, 50px 50px;
   height: calc(100vh - 9vh);
 }
.form-box {
   max-width: 300px;
   background: #f1f7fe;
   overflow: hidden;
   border-radius: 16px;
   color: #010101;
   margin-inline: auto;
 }
 
 .form {
   position: relative;
   display: flex;
   flex-direction: column;
   padding: 12px 24px 24px;
   gap: 16px;
   text-align: center;
 }
 
 /*Form text*/
 .title {
   font-weight: bold;
   font-size: 1.6rem;
 }
 
 .subtitle {
   font-size: 1rem;
   color: #666;
 }
 
 /*Inputs box*/
 .form-container {
   overflow: hidden;
   border-radius: 8px;
   background-color: #fff;
   margin: rem 0 .5rem;
   width: 100%;
 }
 
 .input {
   background: none;
   border: 0;
   outline: 0;
   height: 40px;
   width: 100%;
   border-bottom: 1px solid #eee;
   font-size: .9rem;
   padding: 8px 15px;
 }
 
 .form-section {
   padding: 16px;
   font-size: .85rem;
   background-color: #e0ecfb;
   box-shadow: rgb(0 0 0 / 8%) 0 -1px;
 }
 
 .form-section a {
   font-weight: bold;
   color: #0066ff;
   transition: color .3s ease;
 }
 
 .form-section a:hover {
   color: #005ce6;
   text-decoration: underline;
 }
 
 /*Button*/
 .form button {
   background-color: #0066ff;
   color: #fff;
   border: 0;
   border-radius: 24px;
   padding: 10px 16px;
   font-size: 1rem;
   font-weight: 600;
   cursor: pointer;
   transition: background-color .3s ease;
 }
 
 .form button:hover {
   background-color: #005ce6;
 }